.advantage-block{
    padding: 60px 0 70px;
    .section-title{
        text-align: left;
        color: #ffffff;
        margin-bottom: 45px;
    }
    &__inner{
        position: relative;
        ul{
            display: flex;
            flex-wrap: wrap;
        }
        .item{
            &:not(:last-of-type){
                position: absolute;
            }
            &:last-of-type{
                padding: 170px 0 195px;
                width: 100%;
                text-align: center;
            }
            &:nth-child(1){
                top: 17px;
                left: 110px;
            }
            &:nth-child(2){
                top: 124px;
                left: 0;
            }
            &:nth-child(3){
                left: 110px;
                bottom: 35px;
            }
            &:nth-child(4){
                left: 360px;
                bottom: -7px;
            }
            &:nth-child(5){
                bottom: 70px;
                right: 262px;
            }
            &:nth-child(6){
                bottom: 12px;
                right: 75px;
            }
            &:nth-child(7){
                right: 35px;
                top: 124px;
            }
            &:nth-child(8){
                right: 118px;
                top: -21px;
            }
            &:nth-child(9){
                left: 430px;
                top: 17px;
            }
            &:nth-child(10){
                .item__inner{
                    display: flex;
                    justify-content: center;
                }
            }
            &__inner{
                cursor: pointer;
                .wrap{
                    overflow: hidden;
                    position: relative;
                    color: #ffffff;
                    display: inline-flex;
                    transition: all .3s ease-out;
                    padding-right: 32px;
                    &::before, &::after{
                        position: absolute;
                        content: "";
                        background-color: currentColor;
                        width: 1px;
                        height: 1px;
                        display: block;
                    }
                    &::before{
                        top: 9px;
                        height: 15px;
                        left: calc(100% - 15px);
                    }
                    &::after{
                        width: 15px;
                        top: 16px;
                        left: calc(100% - 22px);
                    }
                    span{
                        background-color: #ffffff;
                        color: #000000;
                        font-size: 13px;
                        padding: 12px 14px;
                        line-height: 19px;
                        border-radius: 10px;
                        display: block;
                        width: 235px;
                        position: absolute;
                        top: 0;
                        left: 100%;
                        opacity: 0;
                        transition: all .3s ease-out;
                        z-index: 1;
                    }
                    &:hover{
                        color: #e08b44;
                        z-index: 2;
                        overflow: inherit;
                        &::before{
                            opacity: 0;
                            transition: all .3s ease-out;
                        }
                        span{
                            opacity: 1;
                            transition: all .3s ease-out;
                        }
                    }
                }
                i{
                    font-size: 52px;
                    margin-bottom: 16px;
                    display: inline-block;
                    color: currentColor;
                }
                p{
                    margin: 0;
                    color: rgba(255,255,255,.5);
                    /* font-size: 15px; */
                    font-size: 13px;
                    line-height: 19px;
                }
                .logo{
                    width: 230px;
                }
                &:hover{
                    z-index: 1;
                    
                }
            }
        }
    }
}
@media(max-width: 1400px){
    .advantage-block__inner {
        .item{
            &:nth-child(1){
                left: 100px;
            }
            &:nth-child(3){
                left: 75px;
                bottom: 0;
            }
            &:nth-child(4){
                left: 228px;
                bottom: -18px;
            }
            &:nth-child(5){
                bottom: 80px;
                right: 207px;
            }
            &:nth-child(6){
                bottom: -18px;
                right: 47px;
            }
            &:nth-child(7){
                right: 7px;
            }
            &:nth-child(8){
                right: 40px;
                top: -21px;
            }
            &:nth-child(9){
                left: 318px;
                top: -5px;
            }
            &:not(:last-of-type){
                width: 165px;
            }
        }
    }
}
@media(max-width: 1199px){
    .advantage-block {
        padding: 40px 0 100px;
    }
    .advantage-block__inner {
        .item{
            &:nth-child(1){
                left: 50px;
                top: -30px;
            }
            &:nth-child(3){
                left: 50px;
            }
            &:nth-child(4){
                left: 222px;
                bottom: -80px;
            }
            &:nth-child(5){
                bottom: 77px;
                right: 148px;
            }
            &:nth-child(6){
                bottom: -15px;
                right: -9px;
            }
            &:nth-child(7){
                right: -13px;
                top: 124px;
            }
            &:nth-child(8){
                right: -1px;
                top: -21px;
            }
            &:nth-child(9){
                left: 210px;
                top: 33px;
            }
            &:not(:last-of-type){
                width: 150px;
            }
            &__inner{
                .logo{
                    width: 182px;
                }
            }
        }
    }
}
@media(max-width: 1024px){
    .advantage-block{
        padding: 35px 0 30px;
        &__inner {
            .item{
                margin-bottom: 30px;
                &:not(:last-of-type){
                    position: static;
                    width: 50%;
                }
                &:last-of-type{
                    width: 50%;
                    padding: 0;
                    text-align: left;
                    .item__inner{
                        justify-content: flex-start;
                    }
                }
                &__inner{
                    .logo{
                        width: 230px;
                    }
                }
            }
        }
    }
}
@media(max-width: 600px){
    .advantage-block {
        padding: 28px 0 10px;
        .section-title{
            margin-bottom: 28px;
        }
        &__inner {
            .item{
                &__inner{
                    .wrap{
                        span{
                            width: 210px;
                        }
                    }
                    p{
                        font-size: 13px;
                    }
                     .logo{
                         width: 140px;
                         
                     }
                }
                &:nth-child(even){
                    .item__inner .wrap{
                        span{
                            top: 100%;
                            left: -65px;
                        }
                    }
                }
                &:nth-child(odd){
                    padding-right: 5px;
                }
            }
        }
    }
}